// Types
import type { PhaseProps } from 'types/game';
// State & Hooks
import { useStep } from 'hooks/useStep';
import { useCache } from 'hooks/useCache';
// Resources & Utils
import { PHASES } from 'utils/phases';
import { useOnSubmitAlienAPIRequest } from './utils/api-requests';
// Icons
import { UfoIcon } from 'icons/UfoIcon';
// Components
import { StepSwitcher } from 'components/steps';
import { PhaseAnnouncement, PhaseContainer } from 'components/phases';
import { Translate } from 'components/language';
import { StepSelectPlayer } from 'components/steps/StepSelectPlayer';
import { TimeHighlight } from 'components/metrics/TimeHighlight';
import { CurseItemHighlight, HieroglyphHighlight, ItemsHighlight } from './components/Highlights';

export function PhaseAlienSelection({ players, state, info }: PhaseProps) {
  const { step, setStep } = useStep();

  // Clear cache from previous games
  useCache({ clearCache: true });

  const onSubmitAlien = useOnSubmitAlienAPIRequest(setStep);

  const status = state.status;

  const announcement = (
    <PhaseAnnouncement
      icon={<UfoIcon />}
      title={<Translate pt="Quem quer ser o aliengena?" en="Who will be the Alien?" />}
      currentRound={state?.round?.current}
      type="overlay"
    />
  );

  const title = <Translate pt="Quem quer ser o aliengena?" en="Who will be the Alien?" />;

  const ruleInstruction = (
    <Translate
      pt={
        <>
          Um aliengena chegou  Terra e no fala nossa lngua. Porm, descobrimos que ele quer abduzir{' '}
          <ItemsHighlight type="positive">{status.needed} objetos</ItemsHighlight>, mas no sabemos quais.
          <br />A cada rodada, devemos mostrar alguns objetos ao aliengena com o objetivo de desvendar um dos
          <HieroglyphHighlight>25 caracteres aliengenas</HieroglyphHighlight> relacionados queles objetos.
          Por exemplo, mostrar uma "bola" e um "pneu" talvez descobriremos o smbolo para "redondo".
          <br />
          Ento o aliengena vai pedir um objeto especfico usando smbolos de sua lngua aliengena.
          <br />
          Teremos <TimeHighlight>{status.timeLeft}</TimeHighlight> chances de entregar todos os objetos ao
          aliengena, mas dentre os 25 objetos, h{' '}
          <CurseItemHighlight type="negative">{status.totalCurses}</CurseItemHighlight> que o aliengena
          considera amaldioado e no quer. Se voc oferece um deles, uma chance adicional  usada naquela
          rodada.
          <br />
          <strong>Um jogador deve ser o aliengena. O VIP selecionar o aliengena.</strong>
        </>
      }
      en={
        <>
          An alien has arrived on Earth and does not speak our language. However, we figured out that they
          want to abduct
          <ItemsHighlight>{status.needed}</ItemsHighlight> objects but we don't know which ones.
          <br />
          Each round, we will show a few objects to the alien in the intent to figure out one of the 25 alien
          characters related to those objects. For example, if we should a "ball" and "tire" we might figure
          out what symbol means "round".
          <br />
          Then, the alien will request an specific object using their language.
          <br />
          We have <TimeHighlight>{status.timeLeft}</TimeHighlight> chances to offer all request objects, but
          among the 25 objects there are{' '}
          <CurseItemHighlight type="negative">{status.totalCurses}</CurseItemHighlight> ones that the alien
          considered cursed. If we offer one of them, we waste one additional chance.
          <br />
          <strong>One player must be the alien, the game master will select it.</strong>
        </>
      }
    />
  );

  return (
    <PhaseContainer
      info={info}
      phase={state?.phase}
      allowedPhase={PHASES.COMUNICACAO_ALIENIGENA.ALIEN_SELECTION}
    >
      <StepSwitcher step={step} players={players}>
        {/* Step 0 */}
        <StepSelectPlayer
          players={players}
          announcement={announcement}
          titleProps={{
            children: <>{title}</>,
            white: true,
          }}
          ruleInstructionProps={{
            children: <>{ruleInstruction}</>,
            type: 'lore',
          }}
          onSubmitPlayer={(playerId) => onSubmitAlien({ alienId: playerId })}
        />
      </StepSwitcher>
    </PhaseContainer>
  );
}
